(function(module) {
try {
  module = angular.module('cross');
} catch (e) {
  module = angular.module('cross', []);
}
module.run(['$templateCache', function($templateCache) {
  $templateCache.put('templates/articles.html',
    '<div class="container" id="taggedArticles"><div class="taggedArticles row"><h1>Analyzed Articles</h1><div class="listitem col-xs-12" ng-repeat="article in taggedArticles"><div class="article listitem col-xs-12" ng-controller="SingleArticleCtrl"><div class="col-xs-12" ng-include="articleTemplate"></div><div class="col-xs-12 tag" ng-repeat="tag in article.ethicstags" ng-controller="SingleTagCtrl" ng-mouseenter="buttons=true" ng-mouseleave="buttons=false"><div ng-include="tagUrl"></div></div></div></div></div></div><div class="container" id="articles"><div class="articleList row"><h1>Add New article</h1><div class="col-xs-12 listitem" ng-controller="NewArticleCtrl"><div ng-include="articleForm"></div></div><h1>Unanalyzed Articles</h1><div class="article listitem col-xs-12" ng-repeat="article in articles" ng-controller="SingleArticleCtrl"><div class="col-xs-12" ng-include="articleTemplate"></div><div class="col-xs-12 col-sm-2 col-sm-offset-10 noDataWrapper"><button class="btn btn-block btn-noData" ng-controller="ArticleNoDataCtrl" ng-click="noData()">No Relevant Data</button></div></div></div></div><div class="container"><div class="row"><h1>Articles Marked As Having No Relevant Data</h1><div class="article listitem col-xs-12" ng-repeat="article in noDataArticles" ng-controller="ArticleRestoreCtrl"><div class="col-xs-12 col-sm-10"><ul><li><h3>{{article.title}}</h3></li><li><a href="{{article.url}}" target="_newtab">{{article.url}}</a></li><li>{{article.notes}}</li></ul></div><div class="col-sm-2 col-xs-12 text-center"><button class="btn btn-block btn-add_analysis" ng-click="noDataRestore()">Restore</button></div></div></div></div><div ng-include="loginModal"></div>');
}]);
})();
